import {CHANGE_NEWS_POR, CHANGE_NEWS_YO} from '../actions';

const initialState = {
    newsYo: {
        "id_0": { title: '  ', date: '02.12.2020', img_url: '/media/tuman.jpg', text: ''},
        "id_1": { title: '   ', date: '11.11.2020', img_url: '', text: '       .       ...'},
        "id_2": { title: '     ...', date: '25.09.2020', img_url: '', text: '    : -        HTML5, CSS3, JavaScript'}
    },
    newsPor: {
        "id_0": { title: '  ', date: '02.12.2020', img_url: '/media/tuman.jpg', text: ''},
        "id_1": { title: '   ', date: '11.11.2020', img_url: '', text: '       .       ...'},
        "id_2": { title: '     ...', date: '25.09.2020', img_url: '', text: '    : -        HTML5, CSS3, JavaScript'}
    }
};
export default function newsReducer(state = initialState, action) {
    console.log('reducer', state, action);
    switch(action.type) {
        case CHANGE_NEWS_YO:
            return {
                newsYo: {
                    ...state.newsYo,
                    [action.payload.newsId]: action.payload.newsState
                },
                ...state.newsPor
            };
        case CHANGE_NEWS_POR:
            return {
                newsPor: {
                    ...state.newsPor,
                    [action.payload.newsId]: action.payload.newsState
                },
                ...state.newsYo
            };
        default:
            return state;
    }
}