import {CHANGE_TEACHERS} from '../../actions';

const initialState = {
        my: {
            ". ." : {
                "id1" : " ..",
                "id2" : " .."
            },
            " .": {
                "id1" : " .."
            },
            "": {
                "id1" : " .."
            },
            ". ": {
                "id1" : " .."
            }
        },
        nemy: {
            " .": {
                "id1" : " .."
            },
            "": {
                "id1" : " .."
            },
            "": {
                "id1" : " ..",
                "id2" : " ..",
                "id3" : " .."
            }
        }
    };

export default function teachersReducer(state = initialState, action) {
    console.log('reducer', state, action);
    switch(action.type) {
        case CHANGE_TEACHERS:
            return {
                    ...state,
                    [action.payload.teaId]: action.payload.teaState
                };
        default:
            return state;
    }
}