export default function scheduleReducer(state = initialState, action) {
    console.log('reducer', state, action);
    switch(action.type) {
        case CHANGE_SCHEDULE:
            return {
                    ...state,
                    [action.payload.schId]: action.payload.schState
                };
        default:
            return state;
    }
}