import {CHANGE_PARENTS} from '../../actions';

const initialState = {
        "id1" : {
            name : " ..",
            par : {
                "id1": " ..",
                "id2": " .."
            }
        },
        "id2": {
            name : " ..",
            par : {
                "id1": " .."
            }
        },
        "id3": {
            name : " ..",
            par : {
                "id1": " .."
            }
        }
    };

export default function parentsReducer(state = initialState, action) {
    console.log('reducer', state, action);
    switch(action.type) {
        case CHANGE_PARENTS:
            return {
                    ...state,
                    [action.payload.Id]: action.payload.State
                };
        default:
            return state;
    }
}